import React, { Component } from "react";
import List from "../List";
import loading from "../../images/loading.gif";
import ModelBox from "../ModelBox";

class Search extends Component {
	handleScroll = () => {
		const windowHeight =
			"innerHeight" in window
				? window.innerHeight
				: document.documentElement.offsetHeight;
		const body = document.body;
		const html = document.documentElement;
		const docHeight = Math.max(
			body.scrollHeight,
			body.offsetHeight,
			html.clientHeight,
			html.scrollHeight,
			html.offsetHeight
		);
		const windowBottom = windowHeight + window.pageYOffset;
		if (windowBottom >= docHeight && !this.props.loadmore) {
			this.props.loadmorePhoto(
				this.props.searchText,
				this.props.page + 1,
				this.props.maxPage
			);
		}
	};
	componentDidMount() {
		window.addEventListener("scroll", this.handleScroll);
	}
	componentWillUnmount() {
		window.removeEventListener("scroll", this.handleScroll);
	}
	render() {
		const { isLoading, photo, error } = this.props;

		if (isLoading)
			return (
				<React.Fragment>
					<ModelBox visible={error}>
						<h1>Có lỗi xảy ra!</h1>
					</ModelBox>
					<div style={style.loadingContainer}>
						<img src={loading} alt="loading" style={style.loadingImg} />
					</div>
				</React.Fragment>
			);

		return (
			<React.Fragment>
				<ModelBox visible={error}>
					<h1>Có lỗi xảy ra!</h1>
				</ModelBox>
				<List
					data={photo}
					cols={3}
					width={900}
					widthCol={300}
					history={this.props.history}
				/>
			</React.Fragment>

		);
	}
}

const style = {
	loadingContainer: {
		width: "100%",
		height: 590,
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		flexDirection: "column"
	},
	loadingImg: {
		width: 100,
		height: 100
	}
};

export default Search;
