import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Item from '../Item';

class List extends Component {
    render() {
        const {data, width, cols, widthCol} = this.props;
        return (
            <div style={{marginTop: 65,display: 'grid', width: width,  gridTemplateColumns: makeTemplateColumns(cols, widthCol), gridGap: 5}}>
            {
                data.map((item, index) => <Item key={index} width={widthCol} item={item} history={this.props.history}/>)
            }
            </div>
        );
    }
}

List.propTypes = {
    data: PropTypes.array.isRequired,
    width: PropTypes.number.isRequired,
    cols: PropTypes.number.isRequired,
    widthCol: PropTypes.number.isRequired
};

function makeTemplateColumns(cols, width){
    let res = '';
    for(let i=0;i<cols;i++){
        res = res + `${width}px `
    }
    return res;
}

export default List;
