import Search from '../components/Search';
import {connect} from 'react-redux'
import { searchLoadMorePhoto } from '../actions';

const mapStateToProps = (state, onwProps) => {
    return {
        photo: state.search.photos,
        isLoading: state.search.isLoading,
        page: state.search.page,
        maxPage: state.search.maxPage,
        searchText: state.search.searchText,
        loadmore: state.search.loadmore,
        error: state.search.isError
    }
}

const mapDispatchToProps = dispatch => {
    return {
        loadmorePhoto: (searchText, page, maxPage) => {
            dispatch(searchLoadMorePhoto(searchText, page, maxPage));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Search);