import React, { Component } from 'react'
import './modelbox.css'

export default class ModelBox extends Component {
  constructor() {
    super();

    this.state = {
      isVisible: false
    };
  }
  componentDidMount() {
    if (this.state.isVisible) {
      this.show();
    }
  }
  show() {
    this.setState({ isVisible: true }, () => this.dialog.showModal());

  }
  hide() {
    this.setState({ isVisible: false }, () => this.dialog.close());
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.visible !== this.state.isVisible) {
      this.setState({ isVisible: nextProps.visible }, () => {
        if (this.state.isVisible) {
          this.show();
        } else {
          this.hide();
        }
      }
      );


    }
  }
  render() {
    return (
      <div>
        <dialog className='dialog' ref={(ref) => this.dialog = ref}>
          {this.props.children}
        </dialog>
      </div>
    )
  }
}
