import Explore from '../components/Explore';
import { connect } from "react-redux";
import { fetchPhotos, loadmorePhoto } from '../actions';


const mapStateToProps = (state, ownProps) => {
    return {
        photo: state.explore.photos,
        page: state.explore.page,
        maxPage: state.explore.maxPage,
        isLoading: state.explore.isLoading,
        loadmore: state.explore.loadmore,
        error: state.explore.isError
    }
}
const mapDispatchToProps = dispatch => {
    return {
        fetchPhotos: () => {
            dispatch(fetchPhotos())
        },
        loadmorePhoto: (page, maxPage) => {
            dispatch(loadmorePhoto(page, maxPage))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Explore);
