import React, { Component, Fragment } from "react";
import "./TopBar.css";
export default class TopBar extends Component {
  state = {
    hide: true
  };

  clickIconSearch = () => {
    this.setState({
      hide: !this.state.hide
    });
  };
  onChange = (e) => {
    this.props.onChange(e.target.value)
  }
  onSubmit = (e) => {
    if(e.keyCode === 13){
      this.props.history.push('/photov2/search')
      this.props.onSubmit(this.props.searchText);
    }
      
  }
  render() {
    return (
      <div className="tbContainer">
        <div className="appName" onClick={() => this.props.history.push('/photov2')}>Photo</div>
        <Fragment>
          <i className="material-icons searchIcon" onClick={this.clickIconSearch}>search</i>
        </Fragment>
        <input type="text" className={`search ${this.state.hide ? 'hide' : ''}`} onChange={this.onChange} value={this.props.searchText} onKeyUp={this.onSubmit}/>
      </div>
    );
  }
}

