import React, { Component } from 'react'

export default class index extends Component {
    state = {
        hover: false
    }
    onMouseEnter = () => {
        this.setState({
            hover: true
        })
    }

    onMouseLeave = () => {
        this.setState({
            hover: false
        })
    }
    render() {
        const { item, width } = this.props;
        return (
            <div style={style.itemContainer} onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave} onClick={() => this.props.history.push(`/photov2/image/${item.id}`)}>
                <div style={{ backgroundImage: `url(${item.url_n})`, backgroundSize: 'cover', width: width, height: 300, backgroundPosition: 'center' }}>
                    {
                        this.state.hover && <div style={{ display: 'flex', flexDirection: 'column', width: width, height: '100%', background: 'linear-gradient(0deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%)', justifyContent: 'flex-end' }}>
                            <h4 style={{ color: 'white', paddingLeft: 10 }}>{item.title}</h4>
                            <div style={{ color: 'white', paddingLeft: 10 }}>Owner: {item.ownername}</div>
                            <div style={{ color: 'white', paddingLeft: 10, marginBottom: 10 }}>Views: {item.views}</div>
                        </div>
                    }
                </div>
            </div>
        )
    }
}

const style = {
    itemContainer: {
        height: 300,
        width: '100%',
        backgroundColor: 'black'
    }
}