import {
    SEARCH_LOADMORE,
    SEARCH_RECEIVE_ERROR,
    SEARCH_RECEIVE_PHOTOS,
    SEARCH_REQUEST_LOADMORE,
    SEARCH_REQUEST_PHOTOS,
    SEARCH_UPDATE_SEARCH_TEXT,
} from "../actions";

const initState = {
    photos: [],
    page: 1,
    perPage: 20,
    maxPage: null,
    isLoading: false,
    isError: false,
    loadmore: false,
    searchText: ''
}

export default function search(state = initState, action) {
    switch (action.type) {
        case SEARCH_REQUEST_PHOTOS:
            return Object.assign({}, state, { isLoading: true })
        case SEARCH_RECEIVE_ERROR:
            return Object.assign({}, state, { isError: true })
        case SEARCH_RECEIVE_PHOTOS:
            return Object.assign({}, state, {
                photos: action.data.photo,
                perPage: action.data.perpage,
                maxPage: action.data.pages,
                isLoading: false,
                isError: false
            })
        case SEARCH_UPDATE_SEARCH_TEXT:
            return Object.assign({}, state, {
                searchText: action.searchText
            })
        case SEARCH_REQUEST_LOADMORE:
            return Object.assign({}, state, {
                loadmore: true
            })

        case SEARCH_LOADMORE:
            return Object.assign({}, state, {
                photos: state.photos.concat(action.data.photos.photo),
                perPage: action.data.photos.perpage,
                page: action.data.photos.page,
                loadmore: false,
                maxPage: action.data.photos.pages
            })

        default: return state;
    }
}
