import {
    REQUEST_PHOTOS,
    RECEIVE_PHOTOS,
    RECEIVE_ERROR,
    LOADMORE,
    REQUEST_LOADMORE
} from '../actions'



const initState = {
    photos: [],
    page: 1,
    perPage: 20,
    maxPage: null,
    isLoading: false,
    isError: false,
    loadmore: false
}

export default function explore(state = initState, action) {
    switch (action.type) {
        case REQUEST_PHOTOS:
            return Object.assign({}, state, {
                isLoading: true
            });
        case RECEIVE_ERROR:
            return Object.assign({}, state, {
                photos: [],
                isError: true
            })
        case RECEIVE_PHOTOS:
            return Object.assign({}, state, {
                photos: action.data.photo,
                perPage: action.data.perpage,
                maxPage: action.data.pages,
                isLoading: false,
                isError: false
            })
        case LOADMORE:
            return Object.assign({}, state, {
                photos: state.photos.concat(action.data.photos.photo),
                perPage: action.data.photos.perPage,
                page: action.data.photos.page,
                loadmore: false,
                maxPage: action.data.photos.pages
            })
        case REQUEST_LOADMORE: {
            return Object.assign({}, state, {
                loadmore: true
            })
        }
        default:
            return state;
    }
}