import React, { Component } from "react";
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import reducers from './reducers'
import TopBar from "./containers/TopBar";
import Explore from "./containers/Explore";
import Search from './containers/Search'
import { BrowserRouter, Route } from 'react-router-dom'
import thunk from 'redux-thunk';
import {createLogger} from 'redux-logger'


import PhotoDetail from "./containers/PhotoDetail";
const logger = createLogger();
const store = createStore(reducers, applyMiddleware(thunk, logger));

export default class App extends Component {
    render() {
        return (
            <Provider store={store}>
                <BrowserRouter>
                    <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                        <Route path='*' render={(props) => <TopBar {...props}/>}/>
                        <Route path="/photov2" exact render={(props) => <Explore {...props} />} />
                        <Route path="/photov2/search" exact render={(props) => <Search {...props}/>} />
                        <Route path='/photov2/image/:id' exact render={(props) => <PhotoDetail {...props}/>}/>
                    </div>
                </BrowserRouter>
            </Provider>
        )
    }
}
