export const REQUEST_PHOTOS = 'REQUEST_PHOTOS'
export const RECEIVE_PHOTOS = 'RECEIVE_PHOTOS'
export const RECEIVE_ERROR = 'RECEIVE_ERROR';
export const LOADMORE = 'LOADMORE';
export const REQUEST_LOADMORE = 'REQUEST_LOADMORE';
export const NONE = 'NONE';


export const SEARCH_REQUEST_PHOTOS = 'SEARCH_REQUEST_PHOTOS';
export const SEARCH_RECEIVE_PHOTOS = 'SEARCH_RECEIVE_PHOTOS';
export const SEARCH_RECEIVE_ERROR = 'SEARCH_RECEIVE_ERROR';
export const SEARCH_LOADMORE = 'LOAD_MORE';
export const SEARCH_REQUEST_LOADMORE = 'SEARCH_REQUEST_LOADMORE';
export const SEARCH_UPDATE_SEARCH_TEXT = 'SEARCH_UPDATE_SEARCH_TEXT'



export function requestPhotos() {
    return {
        type: REQUEST_PHOTOS
    }
}

export function receivePhotos(data) {
    return {
        type: RECEIVE_PHOTOS,
        data: data.photos
    }
}

export function receiveError() {
    return {
        type: RECEIVE_ERROR
    }
}

function requestLoadmore() {
    return {
        type: REQUEST_LOADMORE
    }
}

function loadmore(data) {
    return {
        type: LOADMORE,
        data: data
    }
}

export function fetchPhotos(page = 1) {
    return dispatch => {
        dispatch(requestPhotos())
        return fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=20&page=${page}&format=json&nojsoncallback=1`)
            .then(respone => respone.json())
            .then(json => dispatch(receivePhotos(json)))
            .catch(err => dispatch(receiveError()))
    }
}

export function loadmorePhoto(page, maxPage) {
    if (page > maxPage)
        return {
            type: NONE
        };
    return dispatch => {
        dispatch(requestLoadmore())
        return fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=20&page=${page}&format=json&nojsoncallback=1`)
            .then(respone => respone.json())
            .then(json => dispatch(loadmore(json)))
            .catch(err => dispatch(receiveError()))
    }
}

export function searchRequestPhotos() {
    return {
        type: SEARCH_REQUEST_PHOTOS
    }
}

export function searchReceivePhotos(data) {
    return {
        type: SEARCH_RECEIVE_PHOTOS,
        data: data.photos
    }
}

function searchReceiveError() {
    return {
        type: SEARCH_RECEIVE_ERROR
    }
}

export function updateSearchText(text) {
    return {
        type: SEARCH_UPDATE_SEARCH_TEXT,
        searchText: text
    }
}


export function searchFetchPhotos(searchText) {
    return dispatch => {
        dispatch(searchRequestPhotos());
        return fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=ac9fd35025c930e3ea8d613813e10434&tags=${searchText}&extras=owner_name%2Curl_n%2Cviews&per_page=20&page=1&format=json&nojsoncallback=1`)
            .then(res => res.json())
            .then(json => dispatch(searchReceivePhotos(json)))
            .catch(err => dispatch(searchReceiveError()))
    }
}

function searchRequestLoadmore(){
    return {
        type: SEARCH_REQUEST_LOADMORE
    }
}

function searchLoadmore(json){
    return {
        type: SEARCH_LOADMORE,
        data: json
    }
}

export function searchLoadMorePhoto(searchText, page, maxPage) {
    if (page > maxPage)
        return {
            type: NONE
        }
    return dispatch => {
        dispatch(searchRequestLoadmore());
        return fetch(`https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=ac9fd35025c930e3ea8d613813e10434&tags=${searchText}&extras=owner_name%2Curl_n%2Cviews&per_page=20&page=${page}&format=json&nojsoncallback=1`)
            .then(res => res.json())
            .then(json => dispatch(searchLoadmore(json)))
            .catch(err => dispatch(searchReceiveError()))
    }
}